import { BackProvider } from './../../providers/back/back';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, LoadingController } from 'ionic-angular';
import uuid from 'uuid'

@IonicPage()
@Component({
  selector: 'page-becas',
  templateUrl: 'becas.html',
})
export class BecasPage {

  public becas: any[] = []
  public newBeca: any = {

  }

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public back: BackProvider,
    private toast: ToastController,
    private load: LoadingController
    ) {
  }

  ionViewDidLoad() {
    this.loadTeams()
  }

  loadTeams() {
    //this.back.getUserDetail(this.back.uid).subscribe(data => this.becas = data.teams)
    this.back.getUserBecas(this.back.uid).subscribe(data => this.becas = data, err => this.toast.create({message: 'No se pudo cargar la información de becas...', duration: 2000}).present())
  }

  deleteTeam(id: string) {
    this.back.deleteBeca(id).subscribe(data => this.loadTeams())
  }

  addTeam() {

    const l = this.load.create({content: 'Creando grupo...'})
    l.present()

    this.back.addBeca({
      name: this.newBeca.name,
      creator: this.back.uid,
      code: `ZAMNA-${uuid.v4()}`
    }).subscribe(data => {

      this.back.joinToTeam(this.back.uid, data.id).subscribe(_team => {
        this.toast.create({message: 'Grupo creado correctamente', duration: 2000}).present()
        this.loadTeams()
        l.dismiss()
      })

    }, err => {
      l.dismiss()
      this.toast.create({message: 'No se pudo crear tu grupo...', duration: 2000}).present()
    })

  }

  joinTeam(code: string) {

    const l = this.load.create({content: 'Creando grupo...'})
    l.present()

    this.back.joinTeamByCode(code, this.back.uid).subscribe(data => {
      this.toast.create({message: 'Te uniste correctamente.', duration: 2000}).present()
      this.loadTeams()
      l.dismiss()
    }, err => {
      l.dismiss()
      this.toast.create({message: 'Ocurrió un error al unirte al grupo.', duration: 2000}).present()
    })

  }

  getDescuento(team) {
    if (!team) return 0
    if (!team.members) return 0
    if (team.members.length <= 0) return 0
    if (team.members.length == 2) return 5
    if (team.members.length == 3) return 8
    if (team.members.length == 4) return 10
    if (team.members.length == 5) return 13
    if (team.members.length == 6) return 15
    return 'Contacta con el Admin'
  }

}



